@import "../../styles/common.scss";
@import "../../styles/reset.scss";

.promo {
  margin-bottom: 63px;
}

.promo__content {
  display: flex;
  column-gap: 24px;
  border-radius: 59px;
  background-color: var(--grey);
}


.promo__text {
  font-family: Poppins;
  font-size: 96px;
  font-style: normal;
  font-weight: 900;
  line-height: 125%;

}

.promo__img {
  padding-top: 68px;
  padding-right: 80px;
  flex-shrink: 0;
}

.promo__title {
  font-family: Poppins;
  font-size: 96px;
  font-style: normal;
  font-weight: 900;
  line-height: 120%;
  padding-bottom: 1rem;
  padding-top: 77px;
  padding-left: 120px;
  padding-right: 240px;
 
  flex-shrink: 0;
}

.promo__desc {
  font-size: 32px;
  font-weight: 400;
  line-height: normal;
  letter-spacing: -1.6px;
  height: 58px;
  padding-left: 120px;
}

.promo__btn-wrapper {
  margin-left: 350px;
  margin-top: 20px;
  margin-bottom: 20px;
  border-radius: 10px;
  background: var(--black);
  width: 221px;
  height: 82px;
  flex-shrink: 0;
  // display: block;
}

.promo__btn-wrapper::before{
  // display: block;
  width: 190px;
  height: 32px;
  background-image: url("../../img/icons/decor-yellow.svg");
}

.promo__btn-wrapper p {
  color: var(--white);
  font-size: 30px;
  font-family: Poppins;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: -1.6px;
  width: 221px;
}